<template>
  <div id="answer">
    <div class="answer_header">{{article_data.title}}</div>
    <div class="answer_content">{{text_data.content}}</div>
    <div class="answer_answer">回答来自：<a @click="to('',text_data.answer)">{{text_data.answer}}</a></div>
    <div class="answer_bottom">
      <p>问题暂未解决</p>
      <div class="answer_bottom_p" @click="to">
        <a>在线咨询</a>
        <img src="../../../common/images/箭头@2x.png">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'answer',
  data () {
    return {
      article_data: this.$store.state.CurrentArticle,
      text_data: {
        content: '回答\n',
        answer: ''
      }
    }
  },
  mounted () {
    this.$store.commit('setCurrentTitle', '常见问题')
    this.query()
  },
  methods: {
    to () {
      this.$router.push({
        name: 'onlineService'
      })
    },
    query () {
      this.text_data.content = '回答：\n' +
        '\n' +
        '您好，若您已在我司或其他券商成功开通过创业板权限，则可通过我司财升宝办理转签。相关规定如下：1、转签前提：（1）需在我司下挂或新开立深圳A股账户（即：您原深A账户已开过创业板权限，现在我司新开一个深A账户，也可以办理创业板转签）；（2）已在我司或其他券商成功开通过创业板权限（深圳股东卡处正常状态，不含已休眠、已注销等异常情况）。2、转签流程：（1）业务路径：APP“我”-业务办理-证券账户类-创业板转签，选择需转签的股东卡号，根据页面提示填写第二联系人信息并签署《创业板市场投资风险揭示书》后提交转签申请；（2）受理时间：可7×24小时提交创业板转签申请。温馨提示：若原创业板对应的深圳股东账户处于销户、休眠等非正常状态的，不能通过线上进行创业板转签，您须到营业部柜台按新开创业板业务规则进行办理'
      this.text_data.answer = '服务人员112'
    }
  }
}
</script>

<style scoped>
  #answer {
    position: fixed;
    bottom: 0;
    top: 3rem;
    width: 100%;
    background-color: #F6F7F8;
    color: #333333;
    overflow-y: auto;
  }

  #answer .answer_header {
    line-height: 2rem;
    height: 2rem;
    padding: 1rem;
    font-size: 1.09rem;
    font-weight: 600;
    background-color: #FFFFFF;
  }

  #answer .answer_content {
    color: black;
    font-size: 1rem;
    padding: 0 1rem;
    white-space: pre-line;
    /*overflow-y: auto;*/
    /*height: 62%;*/
    background-color: #FFFFFF;
  }

  #answer .answer_answer {
    text-align: right;
    padding: 1rem;
    background-color: #FFFFFF;
  }

  #answer .answer_answer a {
    color: blueviolet;
  }

  #answer .answer_bottom {

  }

  #answer .answer_bottom p {
    padding: 0 1rem;
    color: #666666;
    font-size: 0.95rem;
  }

  #answer .answer_bottom .answer_bottom_p {
    background-color: #FFFFFF;
    position: relative;
    height: 3.3333rem;
    line-height: 3.3333rem;
    padding: 0 1rem;
  }

  #answer .answer_bottom .answer_bottom_p a {

  }

  #answer .answer_bottom .answer_bottom_p img {
    position: absolute;
    right: 1rem;
    width: 1.2rem;
    top: 1rem;
  }
</style>



